.navbar-wrapper {
    margin-top: 5px; 
  }

  .navbar {
    position: sticky;
    top: 0px; 
    z-index: 1000;
    display: flex;
    justify-content: flex-end;
    padding-right: 20px;
    padding-top: 10px;
    gap: 10px;
    font-family: 'Inter', sans-serif;
  }

  .button {
    background-color: #DB9066;
    border: none;
    color: black;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    border-radius: 50px;
    font-family: 'Inter', sans-serif;
  }

  .button:hover {
    color: #000000;
    background-color: rgb(243, 221, 205);
  }
  